/* General container styling */
.my_chart {
    width: 100%;
    max-width: 1240px;
    margin: 5px auto;
    padding: 5px;
    background-color: #ffffff;
    border: 1px solid #000000;
    border-style: dashed;
  }
  
  /* Flex container for main content */
  .responsive_escon {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-bottom: 20px;
  }
  
  /* Responsive image */
  .responsive_escon img {
    max-width: 100%;
    height: auto;
  }
  
  /* Styling for the machinist files section */
  .machinist_files_container {
    width: 100%;
  }
  
  /* Images inside the machinist section */
  .machinist_files_images {
    display: flex;
    gap: 10px;
    overflow-x: scroll;
    padding-bottom: 10px;
  }
  


  .machinist_files_images img {
    width: 100%;
    max-width: 470px;
    height: auto;
  }
  
  /* Scrollbar customization (optional) */
  .machinist_files_images::-webkit-scrollbar {
    height: 8px;
  }
  
  .machinist_files_images::-webkit-scrollbar-thumb {
    background-color: #888;
    border-radius: 10px;
  }
  
  .machinist_files_images::-webkit-scrollbar-track {
    background-color: #f1f1f1;
  }
  
