  h2 {
    font-size: 24px;
    font-weight: bold;
  }
  
  p {
    font-size: 16px;
    line-height: 1.6;
  }
  
  .image-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 20px;
  }
  
  .image-container {
    flex: 1;
    max-width: 40%;
  }
  
  .image-container h2 {
    font-size: 20px;
    margin-bottom: 10px;
  }
  
  .image-container img {
    width: 100%;
    height: auto;
    margin-bottom: 20px;
  }
  
  .blade-details {
    flex: 1;
    max-width: 55%;
    padding-left: 20px;
  }
  
  .blade-list {
    margin-bottom: 30px;
  }
  
  .blade-list p {
    margin-bottom: 15px;
  }
  
  @media (max-width: 768px) {
    .image-content {
      flex-direction: column;
    }
  
    .image-container, 
    .blade-details {
      max-width: 100%;
      padding-left: 0;
    }
  }
  
  
  
  
  
  
  
  .chart-container {
      width: 100%;
      /* max-width: 1300px; */
      /* margin: 20px auto; */
      padding: 20px;
      background-color: #ffffff;
      border: 1px solid #ffffff;
      border-style: dashed;
    }
    
    h2 {
      color: #7bb3ab;
      text-align: center;
      margin-bottom: 20px;
    }
    
    table {
      width: 100%;
      border-collapse: collapse;
      margin-bottom: 20px;
      padding: 180px;
    }
    
    table, th, td {
      border: 1px solid #fbfbfc;
      background-color: #87c6fa;
      padding: 180px;
    }
    
    th, td {
      padding: 1px;
      text-align: center;
    }
    
    th {
      background-color: #87c6fa;
      color: white;
    }
    
    td {
      background-color: #87c6fa;
      color: #0a0909;
    }
    
    .section-heading {
      background-color: #002266;
      color: white;
      font-weight: bold;
      text-align: left;
      padding: 10px;
    }
    
    @media (max-width: 768px) {
      .chart-container {
        padding: 10px;
      }
    
      table, th, td {
        font-size: 0.8rem;
      }
    }
  
  
     /* cards */
     .why-wikus-section {
      text-align: center;
      margin: 40px auto;
      max-width: 1200px;
    }
    
    h2 {
      color: #333;
      font-size: 28px;
      font-weight: bold;
      margin-bottom: 20px;
    }
    
    .description {
      font-size: 16px;
      color: #666;
      margin-bottom: 40px;
      padding: 0 20px;
    }
    
    .wikus-cards {
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
    }
    
    .wikus-card {
      background-color: #f7f7f7;
      border: 1px solid #ddd;
      border-radius: 8px;
      padding: 20px;
      margin: 10px;
      width: 250px;
      box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
      text-align: center;
    }
    
    .wikus-icon {
      width: 50px;
      height: 50px;
      margin-bottom: 15px;
    }
    
    h3 {
      font-size: 20px;
      color: #000000;
      margin-bottom: 10px;
    }
    
    p {
      font-size: 15px;
      color: #000000;
    }
    
  
    /* cards */
    .why-wikus-section {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      gap: 20px;
      padding: 20px;
      background-color: #f9f9f9;
    }
    
    .why-wikus-card {
      background-color: #fff;
      border-radius: 8px;
      border-color: #87c6fa;
      border-width: thin;
      border-style: solid;
      /* box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); */
      padding: 20px;
      width: calc(25% - 20px); /* Adjust for card width */
      text-align: center;
      transition: transform 0.3s ease, box-shadow 0.3s ease;
    }
    
    .why-wikus-card:hover {
      transform: translateY(-5px);
      box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    }
    
    .why-wikus-card h3 {
      font-size: 1.5em;
      margin: 15px 0;
      color: #333;
    }
    
    .why-wikus-card p {
      font-size: 1em;
      color: #666;
    }
    
    .why-wikus-card svg {
      color: #007bff;
      margin-bottom: 15px;
    }
    
    @media (max-width: 768px) {
      .why-wikus-card {
        width: calc(50% - 20px);
      }
    }
    
    @media (max-width: 480px) {
      .why-wikus-card {
        width: 100%;
      }
    }


    .btn-page-pulse {
      animation-name: pulse;
      animation-duration: 1.5s;
      animation-timing-function: ease-out;
      animation-iteration-count: infinite;
      }
      
      @keyframes pulse {
      0% {
        box-shadow: 0 0 0 0 #87c6fa;
      }
      80% {
        box-shadow: 0 0 0 14px rgba(37, 211, 102, 0);
      }
      }
      